import React, { useRef, useEffect } from 'react';
import Typography from '@material-ui/core/Typography';

import NextLink from '../src/Link';
import MainTemplate from '../src/MainTemplate';

import drawTheTree from '../src/dndtree';
import flare from '../src/flare.json';

export default function VisTest1() {
  let divref = useRef(null);
  useEffect(() => {
    const div = divref.current;
    const tree = drawTheTree(flare);
    div.appendChild(tree);

    return () => {
      div.removeChild(tree);
    }
  }, [divref.current]);

  return (
    <MainTemplate>
      <Typography variant="body1">
        Source: <NextLink href="https://observablehq.com/@d3/tidy-tree">@d3/tidy-tree</NextLink>. Sorry, this is not interactive.
      </Typography>
      <div ref={divref} />
    </MainTemplate>
  );
}
